___CSS_LOADER_EXPORT___.push([module.id, `footer {
  background-color: var(--accent-color);
  padding: 50px 20%;
  display: flex;
  gap: 40px;
  color: var(--accent-text-color);

  svg {
    width: 30px;
    height: 30px;
    color: var(--accent-text-color);
  }

  h2 {
    margin-top: 0;
    margin-bottom: 10px;
  }

  > :first-child {
    flex: 1;
  }

  a {
    color: var(--accent-text-color);
  }

  .svg-link:hover {
    background-color: var(--light-hover-color-1);
  }

  .svg-link:active {
    background-color: var(--light-hover-color-2);
  }
}

.footer__contact-container {
  display: flex;
  align-items: center;
  gap: 15px;
}

.footer__contact-section {
  display: flex;
  flex-direction: column;
  gap: 10px;

  p {
    margin: 0;
    margin-bottom: 5px;
  }
}

@media (max-width: 1200px) {
  body footer {
    padding: 50px 10%;
  }
}

@media (max-width: 880px) {
  footer img {
    width: 300px;
    height: 200px;
  }

  footer picture {
    align-self: center;
  }
}

@media (max-width: 700px) {
  body footer {
    display: flex;
    flex-direction: column;
    padding: 0;
    padding-top: 50px;
    gap: 25px;
  }

  footer img {
    width: 100%;
    height: auto;
    display: block;
  }

  footer .footer__contact-section {
    padding: 0 50px;
  }
}

@media (max-width: 450px) {
  footer h2 {
    text-align: center;
  }

  .footer__contact-container svg {
    display: none;
  }

  footer .socials-container {
    margin-top: 10px;
    justify-content: center;
  }
}
